    svg(role="img")
      use(xlink:href="/assets/svg/sprite.svg#radio1")
    span ラジオボタン1

  input.sw-FormRadio(type="radio" id="radio2" name="radio1" value="1")
  label(for="radio2")
    svg(role="img")
      use(xlink:href="/assets/svg/sprite.svg#radio1")
    span ラジオボタン2ラジオボタン2ラジオボタン2
```

必須項目のエラー表示。

```jade
fieldset.l-Wrapper
  legend.sw-FormLabel
    | ラジオボタン
    em.sw-FormLabel_RequireA （必須）

  input.sw-FormRadio.sw-FormRadio-error(type="radio" id="radio3" name="radio2" value="0")
  label(for="radio3")
    svg(role="img")
      use(xlink:href="/assets/svg/sprite.svg#radio1")
    span ラジオボタン3

  input.sw-FormRadio.sw-FormRadio-error(type="radio" id="radio4" name="radio2" value="1")
  label(for="radio4")
    svg(role="img")
      use(xlink:href="/assets/svg/sprite.svg#radio1")
    span ラジオボタン4

  p.sw-FormHelp
    strong いずれかを選択してください
```

非活性状態。

```jade
fieldset.l-Wrapper
  legend.sw-FormLabel ラジオボタン

  input.sw-FormRadio(disabled type="radio" id="radio5" name="radio3" value="0")
  label(for="radio5")
    svg(role="img")
      use(xlink:href="/assets/svg/sprite.svg#radio1")
    span ラジオボタン5
```
*/
.sw-FormRadio {
  /* デフォルトのラジオボタンを非表示にする。 */
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  overflow: hidden;
  padding: 0;
  clip: rect(0, 0, 0, 0);
}

.sw-FormRadio + label {
  display: table;
  font-size: 1rem;
  line-height: 1.4;
  cursor: pointer;
}

.sw-FormRadio + label > svg {
  display: table-cell;
  vertical-align: top;
  width: 1em;
  height: 1em;
  margin: calc((1.4 - 1) * 0.5em) 0.25em 0 0;
  font-size: 1em;
  fill: #fff;
  border: 1px solid #767676;
  border-radius: 50%;
}

.sw-FormRadio + label > span {
  display: table-cell;
  vertical-align: top;
  width: calc(100% - 1em - 4px);
}

[data-whatinput="keyboard"] .sw-FormRadio:focus + label > svg {
  border-color: #1589ee;
  box-shadow: 0 0 6px 3px #1589ee;
}

[data-whatinput="mouse"] .sw-FormRadio:focus + label > svg,
[data-whatinput="touch"] .sw-FormRadio:focus + label > svg {
  border-color: #767676;
  box-shadow: none;
}

.sw-FormRadio:checked + label > svg,
[data-whatinput="mouse"] .sw-FormRadio:checked + label > svg,
[data-whatinput="touch"] .sw-FormRadio:checked + label > svg {
  border-color: #1e87f0;
  background-color: #1e87f0;
}

@media screen and (-ms-high-contrast: active), print {
  .sw-FormRadio:checked + label > svg,
  [data-whatinput="mouse"] .sw-FormRadio:checked + label > svg,
  [data-whatinput="touch"] .sw-FormRadio:checked + label > svg {
    fill: #000;
    border: 1px solid #000;
  }
}

.sw-FormRadio-error + label > svg {
  fill: #ffecec;
  background-color: #ffecec;
}

.sw-FormRadio:disabled + label {
  cursor: not-allowed;
  opacity: 0.5;
}

