ページタイトルです。

```jade
.l-Wrapper
  h1.sw-Title ページタイトル
```
*/
.sw-Title {
  position: relative;
  font-size: 1.5rem;
  font-weight: bold;
  line-height: 1.2;
  text-align: center;
  color: #be3134;
  margin: 0 0 1.5625rem;
}

@media print, screen and (min-width: 768px) {
  .sw-Title {
    font-size: 1.875rem;
    line-height: 1.4;
  }
}

@media print, screen and (min-width: 768px) {
  .sw-Title {
    margin: 0 0 2.5rem;
  }
}

/*
---
name: sw-Video
namespace: sw-
category: ThirdPartyComponent
---

Youtubeなどの動画をレスポンシブ対応させます。デフォルトで16:9で表示されます。

```jade
.l-Wrapper
  .sw-Video
    .sw-Video_Video.
      <iframe width="560" height="315" src="https://www.youtube.com/embed/1OKZOV-iLj4"></iframe>
    p.sw-Video_Text 動画にキャプション（短い説明文）をつけます。
```

キャプションを中央揃えにします。

```jade
.l-Wrapper
  .sw-Video
    .sw-Video_Video.
      <iframe width="560" height="315" src="https://www.youtube.com/embed/1OKZOV-iLj4"></iframe>
    p.sw-Video_Text.sw-Video_Text-center 動画にキャプション（短い説明文）をつけます。
```

キャプションを右揃えにします。

```jade
.l-Wrapper
  .sw-Video
    .sw-Video_Video.
      <iframe width="560" height="315" src="https://www.youtube.com/embed/1OKZOV-iLj4"></iframe>
    p.sw-Video_Text.sw-Video_Text-right 動画にキャプション（短い説明文）をつけます。
```
*/
.sw-Video {
  margin: 2.5rem 0 0;
}

@media print, screen and (min-width: 768px) {
  .sw-Video {
    margin: 3.125rem 0 0;
  }
}

.sw-Video > :first-child {
  margin-top: 0;
}

.sw-Video > :last-child {
  margin-bottom: 0;
}

.sw-Video_Video {
  display: block;
  overflow: hidden;
  position: relative;
  height: 0;
  padding-bottom: 56.25%;
}

.sw-Video_Video_Item,
.sw-Video_Video iframe,
.sw-Video_Video embed,
.sw-Video_Video object,
.sw-Video_Video video {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}

.sw-Video_Text {
  display: block;
  margin-top: 0.375rem;
  font-size: 0.8125rem;
  line-height: 1.4;
}

.sw-Video_Text-center {
  text-align: center;
}

.sw-Video_Text-right {
  text-align: right;
}

/*
---
name: st-Breadcrumb
namespace: st-
category: Navigation
---

ベーシックなパンくずリストです。`<ol>`タグで指定します。

- 表示中のページと同じ項目には`aria-current="page"`を指定します。

```jade
.l-Wrapper
  ol.st-Breadcrumb
    li.st-Breadcrumb_Item
      a.st-Breadcrumb_Link(href="/") ホーム
    li.st-Breadcrumb_Item
      a.st-Breadcrumb_Link(href="#") カテゴリ名
    li.st-Breadcrumb_Item
      a.st-Breadcrumb_Link(href="#" aria-current="page") ページ名
```
*/
.st-Breadcrumb {
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
  list-style-type: none;
}

.st-Breadcrumb_Item:not(:first-of-type):before {
  content: "/";
  margin: 0 0.5rem;
}

.st-Breadcrumb_Link {
  color: #2b70ba;
  text-decoration: underline;
  display: inline-block;
}

html:not([data-whatinput="touch"]) .st-Breadcrumb_Link:hover {
  color: #2b70ba;
  text-decoration: underline;
}

.st-Breadcrumb_Link[aria-current="page"] {
  color: #000;
  text-decoration: none;
}

